<template>
  <div class="container" v-bind:class="{hide: isActive}">
    <form style="border:1px solid #ccc"  v-on:submit.prevent="addUser">
        <div class="container">
            <h1>Sign Up</h1>
            <p>Please fill in this form to create an account.</p>
            <hr>

            <label for="email"><b>Email</b></label>
            <input type="text" id="email" placeholder="Enter your Email Address" name="email" required v-model="newUser.email">

            <label for="pwd"><b>Enter Password</b></label>
            <input type="password" id="pwd" placeholder="Enter Password" name="pwd" required v-model="newUser.pwd">

            <label for="pwdConfirm"><b>Confirm Password</b></label>
            <input type="password" id="pwdConfirm" placeholder="Confirm Password" name="pwdConfirm" required v-model="newUser.pwdConfirm">        
            
            <div class="clearfix">
            <button type="button" class="cancelbtn">Cancel</button>
            <input type="button" class="regiterbtn" value="newUser" @click="addUser">
            <!-- <input type="submit" class="signupbtn" value="Save"> -->
            <!-- <input type="button" class="signupbtn" value="Demo" @click="hideDiv"> -->
            </div>
        </div>  
    </form>
  </div>
</template>

<script>
    import firebase from 'firebase';

    var config = {
        apiKey: "AIzaSyBYxKuUZQNl79ExzBMFc8F1rys-wzW8VlI",
        authDomain: "vuejs-firebase-35724.firebaseapp.com",
        databaseURL: "https://vuejs-firebase-35724.firebaseio.com",
        projectId: "vuejs-firebase-35724",
        storageBucket: "vuejs-firebase-35724.appspot.com",
        messagingSenderId: "678876741247"
    };
    firebase.initializeApp(config, 'user');

    export default {
        props: ['id'],
        //name: 'App',
       /* firebase: {
            users: usersRef
        },*/
        data() {
            return {   
            isActive: false,
            newUser: {
                email: '',
                pwd: '',
                pwdConfirm: ''
            }
            }
        },
        mounted: function() {
            
        },
        methods: {
            addUser: function(){
                var emailAddress = this.newUser.email,
                    password = this.newUser.pwd,
                    confirmPassword = this.newUser.pwdConfirm;

                if(emailAddress != '' && password != '' && confirmPassword != '') {
                    if(password !== confirmPassword) {
                        alert("Password and Confirm Password is not matched");
                        this.newUser.pwd = '';
                        this.newUser.pwdConfirm = '';
                    } else {
                        firebase.auth().createUserWithEmailAndPassword(emailAddress,password).then((authData) => {
                            alert("User created successfully");
                            //Write code to use authData to add to Users
                        }).catch((error) => {
                            var errorCode = error.code;
                            var errorMessage = error.message;
                            alert(errorMessage);
                        })
                    }
                } else {
                    alert("Please fill all the fields before submmiting the form");
                }
            },

            hideDiv: function() {
                //click of close button
                this.isActive = !this.isActive;       //true - false, false - true          
            }
        }
    }
</script>

<style>
body {font-family: Arial, Helvetica, sans-serif;}
* {box-sizing: border-box}

/* Full-width input fields */
input[type=text], input[type=password] {
    width: 95%;
    padding: 15px;
    margin: 5px 0 22px 0;
    display: inline-block;
    border: none;
    background: #f1f1f1;
}

input[type=text]:focus, input[type=password]:focus {
    background-color: #ddd;
    outline: none;
}

hr {
    border: 1px solid #f1f1f1;
    margin-bottom: 25px;
}

/* Set a style for all buttons */
button {
    background-color: #4CAF50;
    color: white;
    text-decoration-color: #ddd;
    padding: 14px 20px;
    margin: 8px 0;
    border: none;
    cursor: pointer;
    width: 100%;
    opacity: 0.9;
}

button:hover {
    opacity:1;
}

/* Extra styles for the cancel button */
.cancelbtn {
    padding: 14px 20px;
    background-color: #f44336;
}

.regiterbtn {
    padding: 14px 20px;
    background-color: #008000; 
}

/* Float cancel and signup buttons and add an equal width */
.cancelbtn, .regiterbtn {
  float: left;
  width: 40%;
  margin-left: 5%;
}

/* Add padding to container elements */
.container {
    padding: 16px;
}

/* Clear floats */
.clearfix::after {
    content: "";
    clear: both;
    display: table;
}

/* Change styles for cancel button and signup button on extra small screens */
@media screen and (max-width: 300px) {
    .cancelbtn, .regiterbtn {
       width: 100%;
    }
}

.hide {
    display: none;
}

</style>


