<template>
    <div class="container">
        <div class="panel panel-default">
            <div class="panel-heading">
                <h3>Events List</h3>
            </div>
            <div class="panel-body">
                <table class="table table-striped">
                <thead>
                    <tr>
                    <th style="text-align:center">
                        Title
                    </th>
                    <th style="text-align:center">
                        Detail Summary
                    </th>
                    <th style="text-align:center">
                        Detail
                    </th>
                    <th style="text-align:center">
                        Event Date
                    </th>
                     <th style="text-align:center">
                        
                    </th>
                     <th style="text-align:center">
                        
                    </th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(event,index) in events" :key="index">
                    <td width="15%">
                        {{event.title}}
                    </td>
                    <td width="25%">
                        {{event.detailSummary}}
                    </td>
                    <td width="40%">
                        {{event.detail}}
                    </td>
                    <td width="10%">
                        {{event.date}}
                    </td>              
                    <td width="5%">
                        <span class="glyphicon glyphicon-trash" aria-hidden="true" v-on:click="removeEvent(event)"></span>
                    </td>
                    <td width="5%">
                        <span class="glyphicon glyphicon-edit" aria-hidden="true" v-on:click="editEvent(event)"></span>
                    </td>
                    </tr>
                </tbody>
                </table>
            </div>
        </div>


        <div class="panel panel-default">
            <div class="panel-heading">
                <h3>Events</h3>
            </div>
            <div class="panel-body">
                <form id="form" class="form-horizontal">
                    <div class="row">
                        <div class="col-sm-6">
                            <div class="row">
                                <div class="form-group">
                                    <label class="control-label col-sm-4" for="title">Title:</label>
                                    <div class="col-sm-8">
                                        <input type="text" id="eventTitle" class="form-control" v-model="event.title">
                                    </div>
                                </div>
                                <div class="form-group">
                                    <label class="control-label col-sm-4" for="detailSummary">Detail Summary:</label>
                                    <div class="col-sm-8">
                                        <textarea  id="eventDetailSummary" class="form-control" v-model="event.detailSummary"></textarea>
                                    </div>
                                </div>
                                <div class="form-group">
                                    <label class="control-label col-sm-4" for="detail">Detail:</label>
                                    <div class="col-sm-8">
                                        <textarea  id="eventDetail" class="form-control" v-model="event.detail"></textarea>
                                    </div>
                                </div>
                                <div class="form-group">
                                    <label class="control-label col-sm-4" for="detail">Event Date:</label>
                                    <div class="col-sm-8">
                                        <input type="date" class="form-control" id="eventDate"  v-model="event.date">
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="col-sm-6">
                            <div class="row">
                                <div class="form-group">
                                    <label class="control-label col-sm-4" for="title"> Choose Image:</label>
                                    <div class="col-sm-8">
                                    <input type="file" ref="fileInput" id="eventImage" accept="image/*" @change="onFilePicked($event)">
                                    </div>
                                </div>
                                <div class="form-group">
                                    <img :src="event.imageUrl" height="150px" class="img-rounded">
                                </div>
                                <div class="form-group">
                                    <input type="hidden" name="bookKey" class="form-control" v-model="event.keyValue">
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="row">
                        <input type="btn" class="btn btn-primary" value="Save" @click="saveEvent">
                        <input type="btn" class="btn btn-warning" value="Clear" @click="clearEventData">
                    </div>
                </form>
            </div>
        </div>
    </div>
</template>

<script>

    import Firebase from 'firebase'

    let config = {
        apiKey: "AIzaSyBYxKuUZQNl79ExzBMFc8F1rys-wzW8VlI",
        authDomain: "vuejs-firebase-35724.firebaseapp.com",
        databaseURL: "https://vuejs-firebase-35724.firebaseio.com",
        projectId: "vuejs-firebase-35724",
        storageBucket: "gs://vuejs-firebase-35724.appspot.com",
        messagingSenderId: "678876741247"
    } 

    let app = Firebase.initializeApp(config,'event');
    let db = app.database();
    let storage = app.storage()
    let eventsRef = db.ref('events');

    export default {
        
        firebase: {
            events: eventsRef
        },
        data() {
            return {
                event: {
                    title: '',
                    detailSummary: '',
                    detail: '',
                    date: '',
                    keyValue:'',
                    imageUrl: '',
                    fileextension: '',
                    image: null
                }
            }
        },

        methods: {
            onFilePicked (evt) {      
                var files1 = evt.target.files;
                let fileName = files1[0].name;
                if(fileName.lastIndexOf('.') <= 0) {
                    alert('Please upload the Valid file');
                }
                const fileReader = new FileReader();
                fileReader.addEventListener('laod', () => {
                    this.event.imageUrl = fileReader.result;
                })
                fileReader.readAsDataURL(files1[0])
                this.event.image = files1[0];
                this.event.imageUrl = fileReader.result;
                this.event.image = files1[0];
            },

            saveEvent: function() {
               let imageUrls;
                let key;
                let data;
                let self1;
                self1 = this.event;
                if(this.event.title != '' && this.event.detailSummary != '' && this.event.detail != '' && (this.event.image != null || this.event.imageUrl != '')){
                    if(!this.event.keyValue) {
                    eventsRef.push(this.event).then((data) => {
                    key = data.key;
                    return key;
                    }).then(key => {
                    var filename = self1.image.name;
                    console.log(filename);
                    var ext = filename.slice(filename.lastIndexOf('.'));
                    console.log(ext);
                    return storage.ref('events/' + key + ext).put(self1.image);
                    }).then(fileData => {
                        imageUrls = fileData.metadata.downloadURLs[0];
                        var filenamewithext = fileData.metadata.name;
                        var ext = filenamewithext.slice(filenamewithext.lastIndexOf('.'));
                        eventsRef.child(key).update({
                        imageUrl: imageUrls,
                        fileextension: ext
                    })
                    })
                } else if (this.event.keyValue) {
                    const key = this.event.keyValue;
                    if(this.event.image == null && this.event.imageUrl != null) {
                    eventsRef.child(key).set({
                        title: this.event.title,
                        detailSummary: this.event.detailSummary,
                        detail: this.event.detail,
                        date: this.event.date,
                        imageUrl: this.event.imageUrl,
                        fileextension: this.event.fileextension
                    })
                    } else if(this.event.image == null && this.event.imageUrl == null) {
                    eventsRef.child(key).set({
                        title: this.event.title,
                        detailSummary: this.event.detailSummary,
                        detail: this.event.detail,
                        date: this.event.date,
                        fileextension: this.event.fileextension
                    })
                    } else {
                    var filename = this.event.image.name;
                    var ext = filename.slice(filename.lastIndexOf('.'));
                    var oldExt = this.event.fileextension;
                    storage.ref().child('events/' + key + oldExt).delete();
                    storage.ref('events/' + key + ext).put(this.event.image);
                    }
                    
                }
                
                this.event.title = '';
                this.event.detailSummary = '';
                this.event.detail = '';
                this.event.date = '';
                this.event.imageUrl = '';
                this.event.keyValue = '';
                document.getElementById("eventImage").value = "";
                } else {
                    alert("Please enter all the details before save.");
                }
                
            },

            removeEvent: function(event){
                eventsRef.child(event['.key']).remove();
                var ext = event.fileextension;
                storage.ref().child('events/' + event['.key'] + ext).delete();
               this.event.title = '';
                this.event.detailSummary = '';
                this.event.detail = '';
                this.event.date = '';
                this.event.imageUrl = '';
                this.event.keyValue = '';
            },

            editEvent: function(event){
                this.event.title = event.title;
                this.event.detailSummary = event.detailSummary;
                this.event.detail = event.detail;
                this.event.date = event.date;
                this.event.imageUrl = event.imageUrl;
                this.event.keyValue = event['.key'];
                this.event.fileextension = event.fileextension;
            },

            clearEventData: function(){
                this.event.title = '';
                this.event.detailSummary = '';
                this.event.detail = '';
                this.event.date = '';
                this.event.imageUrl = '';
                this.event.keyValue = '';
                this.event.fileextension = '';
                this.event.image = null;
                document.getElementById("eventImage").value = "";
            }

        }
    }
</script>

<style>
table, th, td {
    border: 1px solid black;
}
</style>


