<template>
  <div class="container">
      <form action="/action_page.php">

        <div class="container">
            <label for="uname"><b>Username</b></label>
            <input type="text" id="email" placeholder="Enter your Email Address" name="email" required v-model="login.email">
            <label for="psw"><b>Password</b></label>
             <input type="password" id="pwd" placeholder="Enter Password" name="pwd" required v-model="login.pwd">   
            <input type="button" class="loginbtn" value="Login" @click="loginUser">
            <label>
            <input type="checkbox" checked="checked" name="remember"> Remember me
            </label>
        </div>

        <div class="container" style="background-color:#f1f1f1">
            <button type="button" class="cancelbtn">Cancel</button>
            <span class="psw">Forgot <a href="#">password?</a></span>
        </div>
        </form>
  </div>
</template>

<script>

    import firebase from 'firebase';

    var config = {
        apiKey: "AIzaSyBYxKuUZQNl79ExzBMFc8F1rys-wzW8VlI",
        authDomain: "vuejs-firebase-35724.firebaseapp.com",
        databaseURL: "https://vuejs-firebase-35724.firebaseio.com",
        projectId: "vuejs-firebase-35724",
        storageBucket: "vuejs-firebase-35724.appspot.com",
        messagingSenderId: "678876741247"
    };
    firebase.initializeApp(config, 'login1');

    export default {
        data() {
            return {   
                login: {
                    email: '',
                    pwd: ''
                }
            }
        },
        mounted: function() {
            
        },
        methods: {
            loginUser: function() {
                var email = this.login.email,
                    password = this.login.pwd,
                    me = this;

                if(email === '' || pwd === '') {
                    alert("Please fill all the fields before submmiting the form");
                } else {
                    firebase.auth().signInWithEmailAndPassword(email, password).then(function(firebaseUser) {
                        me.$router.push({name: 'Home'}); 
                    })
                    .catch(function(error) {
                        var errorMessage = error.message;
                        alert(errorMessage);
                    });
                }
            }
        }
    }
</script>

<style>

body {font-family: Arial, Helvetica, sans-serif;}
form {border: 3px solid #f1f1f1;}

input[type=text], input[type=password] {
    width: 95%;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    box-sizing: border-box;
}

input[type=button] {
    background-color: #4CAF50;
    color: white;
    padding: 14px 20px;
    margin: 8px 0;
    border: none;
    cursor: pointer;
    width: 95%;
}

input[type=button]:hover {
    opacity: 0.8;
}

.cancelbtn {
    width: auto;
    padding: 10px 18px;
    background-color: #f44336;
}

.container {
    padding: 16px;
}

span.psw {
    float: right;
    padding-top: 16px;
}

/* Change styles for span and cancel button on extra small screens */
@media screen and (max-width: 300px) {
    span.psw {
       display: block;
       float: none;
    }
    .cancelbtn {
       width: 100%;
    }
}

</style>


