// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import vueFire from 'vuefire'
import App from './App'
import Router from 'vue-router'
import Home from './components/Home'
import RegisterUser from './components/UserRegistration'
import Login from './components/Login'
import EventDetail from './components/EventDetail'

Vue.use(vueFire)
Vue.use(Router)   //register plugin
const routes = [          //----------------------- 1
    { 
      path: '/',    //localhost:8080/
      component: Home, 
      name: "Home"
      // children: [
      //   { 
      //     path: '/register/:id',    //localhost:8080/register/2
      //     component: RegisterUser, 
      //     name: "UserRegistration",
      //     props: true
      //   }
      // ]
    },
    { 
       path: '/register',    //localhost:8080/register
       component: RegisterUser, 
       name: "UserRegistration"
     },
     { 
        path: '/login',    //localhost:8080/login
        component: Login, 
        name: "Login"
      },
      { 
        path: '/eventDetail',    //localhost:8080/login
        component: EventDetail, 
        name: "EventDetail"
      }
    // { 
    //   path: '/comp2', 
    //   component: component2 
    // }
  ];

const myRoutes = new Router({
  routes: routes //vue router takes an array of configs containing path-component mappings--This is ref to 10 line
});

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router: myRoutes,
  components: { App },
  template: '<App/>'
})



